.dcf-topic-view{

    // Thread Title

    .dcf-title{
       .fa{
        margin-right: $spacer-md;
        font-size: $font-sm;
       }
    }

    .dcf-prev-next{
        $fa-padding: 0.5em;
        .fa-prev{
           .fa{
            margin-right: $fa-padding;
           } 
        }
        .fa-next{
            .fa{
             margin-left: $fa-padding;
            } 
         }
    }
    
    .dcf-topic-content{
        width: 100%;

        .dcf-topic-content-top{
            border-bottom: solid 1px $border-inner-color;
        }
    }
    .dcf-topic-info{
        width: 100%;
        border-bottom: solid 1px $border-inner-color;
    }
    @media screen and (min-width: $break-tablet) {
        .dcf-topic-content{
            width: 75%;
        }
        .dcf-topic-info{
            width: 25%;
            border-right: solid 1px $border-inner-color;
            border-bottom: none;
        }
    }
    @media screen and (min-width: $break-desktop) {
        .dcf-topic-content{
            width: 85%;
        }
        .dcf-topic-info{
            width: 15%;
        }
    }
    .dcf-avatar-img{
        aspect-ratio: 1/1;
        border-radius: 50%;
        overflow: hidden;
        max-width: 50px;
        @media screen and (min-width: $break-tablet) {
            max-width: 80%
        }
    }

    .dcf-quick-reply {
        .dcf-subscribe{
            .amaf-chk-subs{
                margin-right: $spacer-xs;
            }
        }
    }
 }